'use client';

import React, { useState, useEffect } from 'react';

// Mock API services for demo
const mockAPI = {
  getCategories: async () => ({
    success: true,
    data: [
      { id: 1, name: 'Cà phê', slug: 'ca-phe' },
      { id: 2, name: 'Trà sữa', slug: 'tra-sua' },
      { id: 3, name: 'Nước ép', slug: 'nuoc-ep' },
      { id: 4, name: 'Sinh tố', slug: 'sinh-to' },
    ]
  }),
  createCategory: async (data) => ({
    success: true,
    data: { id: Date.now(), ...data, slug: data.name.toLowerCase().replace(/\s+/g, '-') }
  }),
  updateCategory: async (id, data) => ({
    success: true,
    data: { id, ...data, slug: data.name.toLowerCase().replace(/\s+/g, '-') }
  }),
  deleteCategory: async (id) => ({
    success: true,
    message: 'Xóa thành công'
  })
};

export default function CategoriesManagement() {
  const [categories, setCategories] = useState([]);
  const [loading, setLoading] = useState(true);
  const [showAddModal, setShowAddModal] = useState(false);
  const [showEditModal, setShowEditModal] = useState(false);
  const [showDeleteModal, setShowDeleteModal] = useState(false);
  const [newCategoryName, setNewCategoryName] = useState('');
  const [editingCategory, setEditingCategory] = useState(null);
  const [editCategoryName, setEditCategoryName] = useState('');
  const [deletingCategory, setDeletingCategory] = useState(null);
  const [error, setError] = useState(null);

  useEffect(() => {
    fetchCategories();
  }, []);

  const fetchCategories = async () => {
    try {
      setLoading(true);
      setError(null);
      const data = await mockAPI.getCategories();
      if (data.success) {
        setCategories(data.data);
      }
    } catch (error) {
      console.error('Lỗi khi tải categories:', error);
      setError('Có lỗi xảy ra khi tải danh sách loại sản phẩm');
    } finally {
      setLoading(false);
    }
  };

  const handleAddCategory = async () => {
    if (!newCategoryName.trim()) {
      alert('Vui lòng nhập tên loại sản phẩm');
      return;
    }

    try {
      const response = await mockAPI.createCategory({ name: newCategoryName.trim() });
      if (response.success) {
        setCategories([...categories, response.data]);
        setNewCategoryName('');
        setShowAddModal(false);
        alert('Thêm loại sản phẩm thành công!');
      }
    } catch (error) {
      console.error('Lỗi khi tạo loại sản phẩm:', error);
      alert('Có lỗi xảy ra khi tạo loại sản phẩm');
    }
  };

  const handleEditCategory = (category) => {
    setEditingCategory(category);
    setEditCategoryName(category.name);
    setShowEditModal(true);
  };

  const handleUpdateCategory = async () => {
    if (!editCategoryName.trim()) {
      alert('Vui lòng nhập tên loại sản phẩm');
      return;
    }

    if (!editingCategory) return;

    try {
      const response = await mockAPI.updateCategory(editingCategory.id, { name: editCategoryName.trim() });
      if (response.success) {
        setCategories(categories.map(cat => 
          cat.id === editingCategory.id 
            ? { ...cat, name: editCategoryName.trim(), slug: response.data.slug }
            : cat
        ));
        setEditCategoryName('');
        setEditingCategory(null);
        setShowEditModal(false);
        alert('Cập nhật loại sản phẩm thành công!');
      }
    } catch (error) {
      console.error('Lỗi khi cập nhật loại sản phẩm:', error);
      alert('Có lỗi xảy ra khi cập nhật loại sản phẩm');
    }
  };

  const handleDeleteCategory = (category) => {
    setDeletingCategory(category);
    setShowDeleteModal(true);
  };

  const confirmDeleteCategory = async () => {
    if (!deletingCategory) return;

    try {
      const response = await mockAPI.deleteCategory(deletingCategory.id);
      if (response.success) {
        setCategories(categories.filter(cat => cat.id !== deletingCategory.id));
        setDeletingCategory(null);
        setShowDeleteModal(false);
        alert('Xóa loại sản phẩm thành công!');
      }
    } catch (error) {
      console.error('Lỗi khi xóa loại sản phẩm:', error);
      alert('Có lỗi xảy ra khi xóa loại sản phẩm');
    }
  };

  const hasModal = showAddModal || showEditModal || showDeleteModal;

  return (
    <div className="min-h-screen bg-gray-50">
      <div className={`p-6 transition-all duration-200 ${hasModal ? 'blur-sm' : ''}`}>
        <div className="flex justify-between items-center mb-6">
          <h1 className="text-2xl font-bold text-gray-900">Loại sản phẩm</h1>
          <button
            onClick={() => setShowAddModal(true)}
            className="bg-green-600 hover:bg-green-700 text-white px-4 py-2 rounded-lg flex items-center gap-2 transition-colors"
          >
            <i className="fas fa-plus"></i>
            Thêm loại
          </button>
        </div>

        {loading ? (
          <div className="flex justify-center items-center h-64">
            <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-green-600"></div>
          </div>
        ) : error ? (
          <div className="bg-red-50 border border-red-200 rounded-lg p-4">
            <div className="flex items-center">
              <i className="fas fa-exclamation-circle text-red-500 mr-2"></i>
              <span className="text-red-700">{error}</span>
            </div>
          </div>
        ) : (
          <div className="bg-white rounded-lg shadow">
            <div className="overflow-x-auto">
              <table className="min-w-full divide-y divide-gray-200">
                <thead className="bg-gray-50">
                  <tr>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider w-20">
                      ID
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Tên loại
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Slug
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider w-40">
                      Thao tác
                    </th>
                  </tr>
                </thead>
                <tbody className="bg-white divide-y divide-gray-200">
                  {categories.length === 0 ? (
                    <tr>
                      <td colSpan={4} className="px-6 py-8 text-center text-gray-500">
                        <i className="fas fa-folder-open text-4xl mb-2 text-gray-300"></i>
                        <p>Chưa có loại sản phẩm nào</p>
                      </td>
                    </tr>
                  ) : (
                    categories
                      .sort((a, b) => a.id - b.id)
                      .map((category) => (
                        <tr key={category.id} className="hover:bg-gray-50 transition-colors">
                          <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                            {category.id}
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap">
                            <div className="text-sm font-medium text-gray-900">{category.name}</div>
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap">
                            <div className="text-sm text-gray-500">{category.slug}</div>
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap text-sm font-medium">
                            <div className="flex gap-3">
                              <button
                                onClick={() => handleEditCategory(category)}
                                className="text-blue-600 hover:text-blue-800 flex items-center gap-1 transition-colors"
                              >
                                <i className="fas fa-edit"></i>
                                Sửa
                              </button>
                              <button
                                onClick={() => handleDeleteCategory(category)}
                                className="text-red-600 hover:text-red-800 flex items-center gap-1 transition-colors"
                              >
                                <i className="fas fa-trash"></i>
                                Xóa
                              </button>
                            </div>
                          </td>
                        </tr>
                      ))
                  )}
                </tbody>
              </table>
            </div>
          </div>
        )}
      </div>

      {/* Modal Thêm loại sản phẩm */}
      {showAddModal && (
        <div 
          className="fixed inset-0 bg-black/50 backdrop-blur-sm flex items-center justify-center z-50"
          onClick={() => setShowAddModal(false)}
        >
          <div 
            className="bg-white rounded-lg shadow-xl w-full max-w-md mx-4 p-6"
            onClick={(e) => e.stopPropagation()}
          >
            <div className="flex justify-between items-center mb-4">
              <h3 className="text-lg font-bold text-gray-900">Thêm loại sản phẩm</h3>
              <button
                onClick={() => setShowAddModal(false)}
                className="text-gray-400 hover:text-gray-600 transition-colors"
              >
                <i className="fas fa-times text-xl"></i>
              </button>
            </div>
            
            <div className="mb-6">
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Tên loại sản phẩm
              </label>
              <input
                type="text"
                value={newCategoryName}
                onChange={(e) => setNewCategoryName(e.target.value)}
                onKeyPress={(e) => e.key === 'Enter' && handleAddCategory()}
                className="w-full border border-gray-300 rounded-lg px-4 py-2 focus:outline-none focus:ring-2 focus:ring-green-500 focus:border-transparent"
                placeholder="Ví dụ: Nước ép trái cây"
                autoFocus
              />
            </div>
            
            <div className="flex justify-end gap-3">
              <button
                onClick={() => setShowAddModal(false)}
                className="px-4 py-2 border border-gray-300 rounded-lg text-gray-700 hover:bg-gray-50 transition-colors"
              >
                Hủy
              </button>
              <button
                onClick={handleAddCategory}
                className="px-4 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700 transition-colors"
              >
                Thêm
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Modal Sửa loại sản phẩm */}
      {showEditModal && editingCategory && (
        <div 
          className="fixed inset-0 bg-black/50 backdrop-blur-sm flex items-center justify-center z-50"
          onClick={() => setShowEditModal(false)}
        >
          <div 
            className="bg-white rounded-lg shadow-xl w-full max-w-md mx-4 p-6"
            onClick={(e) => e.stopPropagation()}
          >
            <div className="flex justify-between items-center mb-4">
              <h3 className="text-lg font-bold text-gray-900">Sửa loại sản phẩm</h3>
              <button
                onClick={() => setShowEditModal(false)}
                className="text-gray-400 hover:text-gray-600 transition-colors"
              >
                <i className="fas fa-times text-xl"></i>
              </button>
            </div>
            
            <div className="mb-6">
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Tên loại sản phẩm
              </label>
              <input
                type="text"
                value={editCategoryName}
                onChange={(e) => setEditCategoryName(e.target.value)}
                onKeyPress={(e) => e.key === 'Enter' && handleUpdateCategory()}
                className="w-full border border-gray-300 rounded-lg px-4 py-2 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                placeholder="Nhập tên loại sản phẩm"
                autoFocus
              />
            </div>
            
            <div className="flex justify-end gap-3">
              <button
                onClick={() => setShowEditModal(false)}
                className="px-4 py-2 border border-gray-300 rounded-lg text-gray-700 hover:bg-gray-50 transition-colors"
              >
                Hủy
              </button>
              <button
                onClick={handleUpdateCategory}
                className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
              >
                Cập nhật
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Modal xác nhận xóa */}
      {showDeleteModal && deletingCategory && (
        <div 
          className="fixed inset-0 bg-black/50 backdrop-blur-sm flex items-center justify-center z-50"
          onClick={() => setShowDeleteModal(false)}
        >
          <div 
            className="bg-white rounded-lg shadow-xl w-full max-w-md mx-4 p-6"
            onClick={(e) => e.stopPropagation()}
          >
            <div className="flex flex-col items-center mb-4">
              <div className="flex items-center justify-center h-14 w-14 rounded-full bg-red-100 mb-4">
                <i className="fas fa-exclamation-triangle text-red-600 text-2xl"></i>
              </div>
              <h3 className="text-lg font-bold text-gray-900 mb-2">Xác nhận xóa</h3>
            </div>
            
            <div className="text-center mb-6">
              <p className="text-gray-600 mb-2">
                Bạn có chắc chắn muốn xóa loại sản phẩm
              </p>
              <p className="text-gray-900 font-semibold mb-2">
                "{deletingCategory.name}"?
              </p>
              <p className="text-sm text-red-600">
                Hành động này không thể hoàn tác!
              </p>
            </div>
            
            <div className="flex justify-end gap-3">
              <button
                onClick={() => setShowDeleteModal(false)}
                className="px-4 py-2 border border-gray-300 rounded-lg text-gray-700 hover:bg-gray-50 transition-colors"
              >
                Hủy
              </button>
              <button
                onClick={confirmDeleteCategory}
                className="px-4 py-2 bg-red-600 text-white rounded-lg hover:bg-red-700 transition-colors"
              >
                Xóa
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}